<template>
  <div >
    <background/>
    <page-top-menu/>
    <div style="width: 100%; height: 80%;">
      <el-carousel style="width:100%; height: 100%;" :interval="4000" arrow="always">
        <el-carousel-item style="width:100%; height: 100%;" v-for="(image, index) in images" :key="index">
          <img :src="image" alt="趣事图片" class="carousel-image" />
        </el-carousel-item>
      </el-carousel>
      <h3>今日趣事</h3>
    </div>
    <message-container/>
  </div>
</template>

<script>
import background from './background.vue';
import MessageContainer from './messageContainer.vue';
import pageTopMenu from './pageTopMenu.vue';

export default{
  components: { background ,pageTopMenu, MessageContainer},
  name:'homePage',
  data(){
    return{
      images:this.loadBuyaImages(),
    }
  },
  methods:{
    loadBuyaImages(){
      const images=[];
      const requireContext=require.context('../../../assets/img/buya/carousel', false, /\.(png|jpe?g|gif|svg)$/);
      requireContext.keys().forEach((fileName) => {
        images.push(requireContext(fileName));
      });
      return images;
    },
  }
}
</script>

<style scoped>
.el-card__body{
  height: 100%;
}
.carousel-image{
  max-width: 100%;
  max-height: 100%; 
  object-fit: cover; 
}
</style>