<template>
  <div class="message-container">
    <span style="height:300px ;width:10%; display: flex">
      <el-menu  class="left-side-menu" @select="handleSelectMessageMenu">
        <el-menu-item :class="{'active-menu-item':messagePageIndex==='0'}" index="0">国家大事</el-menu-item>
        <el-menu-item :class="{'active-menu-item':messagePageIndex==='1'}" index="1">留言板</el-menu-item>
      </el-menu>
    </span>
    <span v-if="messagePageIndex==='0'" style="width:50%; height:300px">
      <el-card style="width:100%;height: 300px;background-color: rgba(132, 222, 111, 0);">
        <span style="font-weight: bold;">今日猪事</span>
        <div style="font-size: medium;text-align: left">
          <li :key="index" v-for="(item,index) in messages">{{item}}</li>
        </div>
      </el-card>
    </span>
    <span v-if="messagePageIndex==='1'" style="width:50%; height:300px;display: flex; flex-direction: column;">
      <el-input v-model="newMessage" :rows="12" maxlength="200" show-word-limit  type="textarea" placeholder="最近有什么猪事分享吗？">
      </el-input>
      <el-button class="message-submit-button" @click="submitMessage" type="primary" >提交</el-button>
    </span>
    <span style="width: 40%;height: 300px;">
      <calendar/>
    </span>
  </div>
</template>

<script>
import calendar from './calendar.vue'
import messageDataSet from '../../../dataset/message.json'
export default{
  name:'messageContainer',
  components:{calendar},
  data(){
    return{
      newMessage:'',
      messages:messageDataSet.messageBoard,
      messagePageIndex:'0',
      disabledInput:false,
    }
  },
  methods:{
    handleSelectMessageMenu(index){
      this.messagePageIndex=index;
    },
    submitMessage(){
      messageDataSet.messageBoard.push(this.newMessage)
      this.newMessage=''
    },

  }
}
</script>

<style scoped>
.message-container{
  display: flex;
  flex:1;
  height: 300px;
}
.left-side-menu{
  background-color: rgb(135, 175, 137);
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.active-menu-item{
  background-color: #9ac7f5 !important;
  color: white; 
}
.message-submit-button{
  width:100%;
  text-align: center;
}
.char-counter{
  position: absolute;
  bottom: 10px; /* 距离输入框底部的距离 */
  right: 10px; /* 距离输入框右侧的距离 */
  color: #999; /* 字数统计的颜色 */
  background-color: rgba(255, 255, 255, 0.8); /* 可选：背景色，增加可读性 */
  padding: 2px 5px; /* 可选：增加一些内边距 */
  border-radius: 4px; /* 可选：圆角 */
}
</style>