<template>
  <div class="page-container">
    <el-menu class="custom-menu" mode="horizontal" @select="handleSelectTopMenu">
      <el-menu-item>
        <img class="menu-image" src='../../../assets/img/buya/icon/buya1.png' alt="Menu Icon"/>
      </el-menu-item>
      <el-menu-item index="0">首页</el-menu-item>
      <el-menu-item index="1">视频</el-menu-item>
      <el-menu-item index="2">游戏</el-menu-item>
      <el-menu-item>
        <el-input v-model="serchText" style="width: 150px" placeholder="搜索">
          <template #append>
          <el-button class="el-icon-search" @click="searchContent"></el-button>
          </template>
        </el-input>
      </el-menu-item>
    </el-menu>
    <el-menu class="right-menu" mode="horizontal" @select="handleSelectTopRightMenu">
      <el-menu-item index="0">登录</el-menu-item>
      <el-menu-item index="1">注册</el-menu-item>
    </el-menu>`
  </div>
</template>

<script>

export default {
  name:'pageTopMenu',
  data(){
    return{
      serchText:'',

    }
  },
  methods:{
    searchContent(){
      console.log('搜索中')
    },
    handleSelectTopMenu(index){
      if(index==='0'){
        this.$router.push({ name: 'Home' });        
      }else if(index==='1'){
        this.$router.push({ name: 'Video' });      
      }else if(index==='2'){
        this.$router.push({name:'Game'});
      }
    },
    handleSelectTopRightMenu(index){
      if(index==='0'){
        this.$router.push({ name: 'Login' });
      }
    },
  }
}
</script>

